.login {
  width: 100%;
  height: 100%;
  background: url(./image/bg.jpg);
  background-size: 100% 100%;

  .login-header {
    display: flex;
    align-items: center;
    height: 80px;
    background-color: #324d69;
    .logo {
      margin: 0 20px 0 20px;
      width: 40px;
      height: 40px;
    }
    .title {
      color: #ffffff;
      font-size: 30px;
    }
  }

  .login-content {
    padding: 10px 40px;
    width: 450px;
    height: 250px;
    background-color: #fff;
    margin: 40px auto;
    border-radius: 5px;
    h1 {
      text-align: center;
      font-size: 30px;
      font-weight: bold;
    }
    .login-form {
      .login-form-button {
        width: 100%;
        border-radius: 5px;
      }
    }
  }
}

@primary-color: #1DA57A;